<template>
  <div class="oh p15">
    <div class="col">
      <a :href="'#/service-apply?id=' + item.service_id" v-if="isShow(item.service_title)"
         class="tc p10 pb5 service-item"
         v-for="(item, index) in listData"
         :key="item.service_id">
        <img v-lazy="item.imgUrl.op_spic" width="42" height="42" alt="">
        <div class="mt5 f13">{{ item.service_title }}</div>
      </a>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'serviceGrid',
    props: {
      showServiceName: {
        default() {
          return ['培训及教育服务', '知识产权规划及资质管理服务', '技术及人才服务', '多功能公关服务平台', '空间开发服务', '“O2O”服务平台', '咨询顾问服务', '创新金融服务', '产业政策服务', '高级政务服务']
        },
        type: Array
      }
    },
    data() {
      return {
        listData: []
      }
    },
    created() {
      this.pullData()
    },
    methods: {
      isShow(name) {
        return this.showServiceName.indexOf(name) >= 0
      },
      pullData() {
        this.axios.get(this.apiConfig.unionServiceList).then(res => {
          if (res.s === 1) {
            this.$store.dispatch('UPDATE_LOADINGSTATE', false)
            this.listData = res.d
          }
        })
      }
    }
  }
</script>
<style scoped lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  .col {
    margin-bottom: rem(-16px);
    overflow: hidden;
    border-radius: rem(6px);
  }

  .service-item {
    display: block;
    background-color: #fff;
    float: left;
    width: 33.3%;
    height: rem(106px);
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    &:nth-child(3n) {
      border-right: none;
    }
  }
</style>
